const MainRouets = [
  {
    path:'/honm',
    alias: '/',
    name:'Honm',
    meta:{indexe:0},
    component:() => import('../views/Honm')
  },
  {
    path:'/caidan',
    name:'Caidan',
    meta:{indexe:1},
    component:() => import('../views/Caidan')
  },
  {
    path:'/guowu',
    name:'Guowu',
    meta:{indexe:2},
    component:() => import('../views/Guowu')
  },
  {
    path:'/shousuo',
    name:'Shousuo',
    meta:{indexe:3},
    component:() => import('../views/Shousuo.vue')
  },
  {
    path:'/my',
    name:'My',
    meta:{indexe:4},
    component:() => import('../views/My')
  },
]

export const routes = [  
  {
      path: '/',
      name: 'Main',
      component: () => import('../views/Main.vue'),

      children: MainRouets
    },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path:'/detail:pid',
    name:'Detail',
    component:() => import('../views/Detail.vue')

  },
  {
    path:'/kef',
    name:'Kef',
    component:() => import('../views/Kef.vue')
  },
  {
    path:'/addlist',
    name:'Addlist',
    component:() => import('../views/Addlist.vue')
  },
  {
    path:'/addedit/:aid?',
    name:'Addedit',
    component:() => import('../views/Addedit.vue')
  },
  {
    path:'/geren',
    name:'Geren',
    component:() => import('../views/Geren.vue')
  },
  {
    path:'/shoucang',
    name:'Shoucang',
    component:() => import('../views/Shoucang.vue')
  },
  {
    path:'/pay',
    name:'Pay',
    component:() => import('../views/Pay.vue')
  },
  {
    path:'/order',
    name:'Order',
    component:() => import('../views/Order.vue')
  },
  {
    path:'/anquan',
    name:'Anquan',
    component:() => import('../views/Anquan.vue')
  },
  {
    path:'/zhaohui',
    name:'Zhaohui',
    component:() => import('../views/Zhaohui.vue')
  },
]