import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

//修改state唯一途径，通过mutations

export default new Vuex.Store({
  state: {
    shopbagCount: 0
  },

  mutations: {
    changeShopbagCount(state, data) {
      //state: vuex状态state
      //data: 载荷(参数)

      // 
      state.shopbagCount = data;
    }
  }
})
